/* Navbar.css */

/* Base styles */
.navbar-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000; /* Ensure the navbar is above other content */
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #333; /* Example background color */
  color: white; /* Example text color */
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    background-color: teal;
}

.search-input {
    width: 300px;
    background-color: white;
    color: teal;
    border: none;
    border-radius: 20px;
    padding: 0.5rem 1rem;
    margin-right: 1rem;
}

/* .search-input:focus {
    outline: none;
} */

button {
    font-weight: bold;
}

/* Responsive styles */
@media screen and (max-width: 768px) {
    .navbar {
        flex-direction: column;
        align-items: flex-start;
    }

    .search-input {
        width: auto;
        margin-right: 0;
        margin-bottom: 1rem;
    }
}

  
  /*For Footer*/
  /* Footer.css */
.footer {
    background-color: #333;
    color: #fff;
    padding: 2rem 0;
  }
  
  .footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  
  .footer-section {
    padding: 1rem;
  }
  
  .footer-section h2 {
    font-size: 1.2rem;
    margin-bottom: 1rem;
  }
  
  .footer-section p {
    font-size: 0.9rem;
    margin-bottom: 1rem;
  }
  
  .contact span {
    display: block;
    margin-bottom: 0.5rem;
  }
  
  .links ul {
    list-style-type: none;
    padding: 0;
  }
  
  .links li {
    margin-bottom: 0.5rem;
  }
  
  .links a {
    color: #fff;
    text-decoration: none;
  }
  
  .follow .social-links a {
    color: #fff;
    margin-right: 1rem;
  }
  
  .follow .social-links a:hover {
    color: #66c0f4;
  }
  
  .footer-bottom {
    text-align: center;
    margin-top: 1rem;
  }
  